/* src/LandingPage.css */
.landing-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding: 0 20px;
    text-align: center; /* Center the text in the left-side */
  }
  
  .left-side, .right-side {
    padding: 20px;
  }
  
  .right-side {
    display: flex;
    flex-direction: column; /* Stack the image and button vertically */
    align-items: center;
    justify-content: center;
  }
  
  .right-side img {
    width: 200px; /* Make the image a square by setting both width and height */
    height: 200px;
    border-radius: 50%; /* This turns the square image into a circle */
    object-fit: cover; /* Ensures the image fills the circle without distortion */
    margin-bottom: 20px; /* Space between the image and the button */
  }
  
  .continue-button {
    padding: 12px 30px;
    font-size: 16px;
    background-color: #4CAF50; /* A nice green color */
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .continue-button:hover {
    background-color: #45a049; /* Darker shade of green on hover */
  }
  